

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import xianp from '@/components/xianp'
import circle from '@/components/circle'
import mine from '@/components/mine'

import Bus from './eventbus'

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import XButton from 'vux/src/components/x-button/index.vue'


Vue.component('x-button', XButton)

Vue.use(VueRouter)
Vue.use(LoadingPlugin)

const routes = [
{
path: '/',
name: 'app',
component: App
},
{
path: '/xianp',
name: 'xianp',
component: resolve => (['@/components/xianp'], resolve)
},
{
path: '/circle',
name: 'circle',
component: resolve => (['@/components/circle'], resolve)
},
{
path: '/mine',
name: 'circle',
component: resolve => (['@/components/mine'], resolve)
}
]
const router = new VueRouter({
routes
})

FastClick.attach(document.body)

Vue.config.devtools = true; 
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



